<template>
  <div class="home">
      <div class="card">
        <el-card shadow="always"> {{time}} </el-card>
      </div>
  </div>
</template>

<script>
import { ElCard } from 'element-plus'
import {computed} from "vue";


export default {
  name: 'HomeView',
  components: {
    ElCard
  },
  mounted () {
    //这个属性就可以，在里面声明初始化时要调用的方法即可
    // we can implement any method here like

  },
  setup: function () {
    const date = new Date("2022/09/22");//定义开始时间
    const today = new Date();//定义结束时间
    let allTime = new Date();
    //getTime() 返回从 1970 年 1 月 1 日至今的毫秒数。
    //1秒=1000毫秒  3600=1小时*1分钟   24=24小时
    const time = computed(() => {
      let time = parseInt((today.getTime() - date.getTime()) / (1000 * 3600 * 24));
      return "我们在一起" + Math.round(time / 365) + "年" + time
          + "天啦!!!";
    })
    // 暴露 state 到模板
    return {
      date,
      time,
      allTime
    }
  }
}
</script>

<style scoped>
.home {
  text-align: center;
}
/* 在大于或等于 1200 像素的屏幕上 */
@media screen and (min-width: 1200px) {
  .card {
    width: 80vw;
    display: inline-block;
    margin-top: 20vh;
  }
}

/* 在 600 像素或更小的屏幕上，将背景色设置为橄榄色 */
@media screen and (max-width: 600px) {
  .card {
    width: 80vw;
    display: inline-block;
    margin-top: 20vh;
  }
}
</style>
